import axios from 'axios';
import React, { useEffect, useState } from 'react'
import { Helmet } from 'react-helmet';

export default function People() {
  const [People, setPeople] = useState([]);
  let PeopleMovies=async ()=>{
    let {data}=await axios.get('https://api.themoviedb.org/3/trending/person/day?api_key=592d5558fe91383c9979c4a7c357bfee');
    setPeople(data.results);
    console.log(data.results);
  }
  useEffect(() => {
    PeopleMovies();
  }, [])
  
  return (
    <>
    
       <Helmet>
                <meta charSet="utf-8" />
                <title>Persons</title>
                
            </Helmet>
    <div className="row my-5 py-2">
<div className="col-md-4  justify-content-center flex-column d-flex align-items-center">
  <div className="static mb-5 ">
    <div className={`  w-25 `}></div>
    <h3>Trending</h3>
    <h3>Persons</h3>
    <h4>To watch now</h4>
    <span >most watched persons by day</span>
    <div className={`  w-100 `}></div>
  </div>
</div>
 {People.map((item,index)=>
  <div key={index} className="col-md-2">
  <div className="item m-2 position-relative">
    <img className='w-100' src={`https://image.tmdb.org/t/p/original/${item.profile_path}`} alt="" />
    <h2 className='h6'>{item.original_title} {item.name}</h2>
    <span className='text-white position-absolute top-0 end-0 bg-info p-2'>{item.popularity.toFixed(1)} </span>
  </div>
</div>
)}
</div>
</>
  )
}
